        record.pro_show_display_type = JSON.stringify(this.registerForm.value.pro_show_display_type);
        record.pro_show_display_view = JSON.stringify(this.registerForm.value.pro_show_display_view);
        record.pro_show_display_selection = JSON.stringify(this.registerForm.value.pro_show_display_selection);
